import React from "react";
import { RotatingLines } from "react-loader-spinner";
import nearIcon from "../styles/icons/near_icon.svg";
import usdtIcon from "../styles/icons/usdt_icon.svg";
export const Balances = (props) => {
  if (props.balances.length === 0) {
    return (
      <div>
        <h3 className="no-data">No balances here yet. . .</h3>
      </div>
    );
  }

  if (Object.keys(props.balances[0]).length === 0) {
    localStorage.setItem("isNeedToBeDisabled", true);
    return (
      <div className="loader">
        <RotatingLines
          strokeColor="#1aafa6"
          strokeWidth="2"
          animationDuration="0.75"
          width="50"
          visible={true}
        />
      </div>
    );
  }

  const balancesList = props.balances.map((item) => {
    localStorage.removeItem("isNeedToBeDisabled");
    return (
      <div className="balance-row" key={item.id}>
        {item.icon ? (
          <img src={item.icon} className="balance-coin-icon" />
        ) : (
          <></>
        )}
        <li key={item.id} className="balance-list">
          <div className="balance-token-block">
            <p>{item.token}</p>
          </div>

          <p className="balance-amount">{item.amount}</p>

          <p>{item.token}</p>
        </li>
      </div>
    );
  });
  if (props.balances.length > 4) {
    return <ul className="balances-ul scrollable-block">{balancesList}</ul>;
  }
  return <ul className="balances-ul">{balancesList}</ul>;
};
