import React, { useState, useCallback, useEffect } from "react";
import * as nearAPI from "near-api-js";
import axios from "axios";
import Big from "big.js";
import logo from "../styles/images/logo.png";
import nearIcon from "../styles/icons/near_icon.svg";
import usdtIcon from "../styles/icons/usdt_icon.svg";
import BuyCheckmarkIcon from "../styles/icons/buy_checkmark_icon.svg";
import SellCheckmarkIcon from "../styles/icons/sell_checkmark_icon.svg";
import { Orders } from "./Orders";
import { HiChevronDown } from "react-icons/hi";
import { BsArrow90DegRight, BsChevronDoubleLeft } from "react-icons/bs";
import { RotatingLines } from "react-loader-spinner";

import "../styles/landing.css";
import "../styles/app.css";
import { Balances } from "./Balances";
import { OrderBook } from "./OrderBook";
import tokenConfig from "../../config/tokenConfig";
import { transactions } from "near-api-js";
import { PublicKey } from "near-api-js/lib/utils";

const BN = require("bn.js");

const ONE_YOCTO = 1;
const TGAS = new BN(250000000000000);
const DEPOSIT_GAS = new BN(10000000000000);
// Hardcoded orderList
const ORDER_LIST = [
  {
    id: 1,
    type: "buy",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    status: "Cancelled",
  },
  {
    id: 2,
    type: "buy",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    status: "Cancelled",
  },
  {
    id: 3,
    type: "sell",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    status: "Cancelled",
  },
  {
    id: 4,
    type: "sell",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    sell_token: "USDT",
    status: "Finished",
  },
  {
    id: 5,
    type: "buy",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    status: "Finished",
  },
  {
    id: 6,
    type: "sell",
    buy_amount: 100,
    buy_token: "NEAR",
    sell_amount: 12,
    sell_token: "USDT",
    status: "Finished",
  },
];

// Hardcoded balances
const BALANCES = [
  { id: 1, token: "NEAR", icon: nearIcon, amount: 243 },
  { id: 2, token: "USDT", icon: usdtIcon, amount: 243 },
];

const ORDER_BOOK = [
  { id: 1, price: 100, amount: 10 },
  { id: 2, price: 200, amount: 14 },
  { id: 3, price: 500, amount: 18 },
  { id: 4, price: 800, amount: 22 },
  { id: 5, price: 50, amount: 22 },
  { id: 6, price: 400, amount: 15 },
  { id: 7, price: 440, amount: 116 },
];

export const MainApp = ({
  contract,
  currentUser,
  config,
  wallet,
  provider,
  near,
}) => {
  const [balances, setBalances] = useState([{}]);
  const [tokens, setTokens] = useState([{}]);
  const [notStableTokenData, setNotStableTokenData] = useState([{}]);
  const [currentNotStableToken, setCurrentNotStableToken] = useState({});
  const [wUSDTTokenData, setWUSDTTokenData] = useState({});
  const [orderBook, setOrderBook] = useState(null);
  const [userAction, setUserAction] = useState("buy");
  const [orderBookAction, setOrderBookAction] = useState("buy");
  const [userAmount, setUserAmount] = useState("");
  const [userPrice, setUserPrice] = useState("");
  const [total, setTotal] = useState("");
  const [errorText, setErrorText] = useState("");
  const [orders, setOrders] = useState(null);
  const [orderComponent, setOrderComponent] = useState();
  const [tokensListAction, setTokensListAction] = useState();

  let SHORT_NAME = "";
  if (currentUser) {
    let i = 0;
    while (i !== 13) {
      SHORT_NAME += currentUser.accountId[i];
      i++;
    }
  }

  useEffect(() => {
    if (currentUser) {
      getFtBalances();
      getAllOrders();
    }
    getPairs();
    getOrdersToOrderBook();
    sortOrders();
  }, []);

  useEffect(() => {
    getOrdersToOrderBook();
  }, [orderBookAction, tokensListAction]);

  const getOrdersToOrderBook = async () => {
    let buyToken;
    let sellToken;
    const result = [];

    setCurrentNotStableToken(
      JSON.parse(localStorage.getItem("notStableToken"))
    );
    if (
      localStorage.getItem("wusdtData") &&
      localStorage.getItem("notStableToken") &&
      orderBookAction === "buy"
    ) {
      sellToken = JSON.parse(localStorage.getItem("wusdtData")).address;
      buyToken = JSON.parse(localStorage.getItem("notStableToken")).address;
    } else if (
      localStorage.getItem("wusdtData") &&
      localStorage.getItem("notStableToken") &&
      orderBookAction === "sell"
    ) {
      sellToken = JSON.parse(localStorage.getItem("notStableToken")).address;
      buyToken = JSON.parse(localStorage.getItem("wusdtData")).address;
    } else {
      return setOrderBook(null);
    }

    const ordersToMatch = contract.get_orders_to_orderbook({
      sell_token: sellToken,
      buy_token: buyToken,
    });

    Promise.all([ordersToMatch]).then((value) => {
      const orders = value[0];

      return setOrderBook(orders);
    });

    setOrderBook(undefined);
  };

  const initTokenContract = async () => {
    let stableTokenConfig;
    const orderMsg = JSON.parse(localStorage.getItem("msg"));
    if (orderMsg.hasOwnProperty("sell_token") && userAction === "buy") {
      stableTokenConfig = tokenConfig(process.env.NEAR_ENV || "testnet");
    } else if (orderMsg.hasOwnProperty("sell_token") && userAction === "sell") {
      stableTokenConfig = tokenConfig(process.env.NEAR_ENV || "testnet");
      stableTokenConfig.contractName = currentNotStableToken.address;
    } else if (userAction === "buy") {
      stableTokenConfig = tokenConfig(process.env.NEAR_ENV || "testnet");
    } else if (userAction === "sell") {
      stableTokenConfig = tokenConfig(process.env.NEAR_ENV || "testnet");
      stableTokenConfig.contractName = currentNotStableToken.address;
    }

    const keyStore = new nearAPI.keyStores.BrowserLocalStorageKeyStore();

    const near = await nearAPI.connect({ keyStore, ...stableTokenConfig });

    const wallet = new nearAPI.WalletConnection(near);

    const tokenContract = new nearAPI.Contract(
      wallet.account(), // the account object that is connecting
      stableTokenConfig.contractName,
      {
        // name of contract you're connecting to
        viewMethods: ["ft_balance_of"], // view methods do not change state but usually return a value
        changeMethods: ["ft_transfer_call", "storage_deposit"], // change methods modify state
        sender: wallet.account(), // account object to initialize and sign transactions.
      }
    );

    return { tokenContract, stableTokenConfig };
  };

  const contractQuery = async (contract, method, args = {}) => {
    const rawResult = await provider.query({
      request_type: "call_function",
      account_id: contract,
      method_name: method,
      args_base64: Buffer.from(JSON.stringify(args)).toString("base64"),
      finality: "optimistic",
    });
    return JSON.parse(Buffer.from(rawResult.result).toString());
  };

  const onChange = async () => {};

  const getPairs = async () => {
    let pairs = await contract.get_pairs();
    let balancePairs = null;
    if (currentUser) {
      balancePairs = await getFtTokens(currentUser.accountId);
    }
    let validPairs = [];

    const wusdtMeta = await contractQuery("wusdt.testnet", "ft_metadata");
    localStorage.setItem(
      "wusdtData",
      JSON.stringify({
        symbol: wusdtMeta.symbol,
        icon: wusdtMeta.icon,
        address: "wusdt.testnet",
      })
    );

    pairs.forEach(async (pair) => {
      const sellToken = pair.split("#")[0];
      const buyToken = pair.split("#")[1];

      if (
        sellToken !== "wusdt.testnet" &&
        validPairs.indexOf(sellToken) === -1
      ) {
        validPairs.push(sellToken);
      } else if (
        buyToken !== "wusdt.testnet" &&
        validPairs.indexOf(buyToken) === -1
      ) {
        validPairs.push(buyToken);
      }
    });

    if (balancePairs !== null) {
      for (let pair of balancePairs) {
        try {
          const balance = await Promise.all([
            contractQuery(pair, "ft_balance_of", {
              account_id: currentUser.accountId,
            }),
          ]);

          if (validPairs.indexOf(pair) === -1 && balance[0] !== "0") {
            validPairs.push(pair);
          }
        } catch (e) {
          console.log(e);
          continue;
        }
      }
    }

    const result = [];

    if (localStorage.getItem("notStableToken") === null) {
      const firstVisibleSymbol = await contractQuery(
        validPairs[validPairs.length - 1],
        "ft_metadata"
      );

      setCurrentNotStableToken({
        symbol: firstVisibleSymbol.symbol,
        icon: firstVisibleSymbol.icon,
      });
    }

    for (const tokenAddress of validPairs) {
      try {
        const tokenMeta = await contractQuery(tokenAddress, "ft_metadata");

        if (tokenAddress !== "wusdt.testnet") {
          result.push({
            symbol: tokenMeta.symbol,
            icon: tokenMeta.icon,
            address: tokenAddress,
          });

          localStorage.setItem(
            "notStableToken",
            JSON.stringify({
              symbol: tokenMeta.symbol,
              icon: tokenMeta.icon,
              address: tokenAddress,
            })
          );

          // setNotStableTokenData(result);
          // setCurrentNotStableToken({
          //   symbol: tokenMeta.symbol,
          // });
          // if (localStorage.getItem("notStableToken") === null) {
          //   setCurrentNotStableToken({
          //     symbol: tokenMeta.symbol,
          //   });
          // }
        }
      } catch (e) {
        console.error(e);
        continue;
      }
    }

    setTokens(result);
  };

  const onDropDownClick = () => {
    const list = document.getElementById("dd-list");
    const checkmark = document.getElementById("dd-icon");

    if (list.classList[0] === "dd-list") {
      list.classList = [];
      list.classList.add("not-visible");
      checkmark.classList = [];
      checkmark.classList.add("dd-closed-icon");
      return;
    }
    list.classList = [];
    list.classList.add("dd-list");
    checkmark.classList = [];
    checkmark.classList.add("dd-opened-icon");
  };

  const signIn = async () => {
    wallet.requestSignIn({
      contractId: contract.contractName,
      methodNames: [
        "match_order",
        "add_order",
        "add_filled_order",
        "get_or_create_fee_info",
        "take_fee",
        "set_fee",
        "transfer_earned_fees",
        "callback_on_send_tokens_to_ext_account",
        "callback_on_send_tokens_to_maker",
        "callback_after_deposit",
        "remove_order",
        "internal_remove_order",
      ],
      successUrl: "http://jove-06.github.io/web-crypto-market/#/app", //http://jove-06.github.io/web-crypto-market/#/app
      failureUrl: null,
    });
  };
  const signOut = () => {
    wallet.signOut();
    localStorage.removeItem("messages");
    localStorage.removeItem("numberOfPages");
    window.location.reload();
  };

  //! GET ORDERS
  const getAllOrders = async () => {
    if (localStorage.getItem("seemoreAction") === null) {
      localStorage.setItem("seemoreAction", "2");
    }

    const pairs = await contract.get_pairs(); //!
    const finishedPairs = await contract.get_finished_pairs();
    finishedPairs.map((orderPair) => {
      if (!pairs.includes(orderPair)) {
        pairs.push(orderPair);
      }
    });

    if (pairs.length === 0) {
      return setOrders(null);
    }

    const result = [];
    const finishedResultOrders = [];
    pairs.forEach(async (item) => {
      const sellTokenAddress = item.split("#")[0];
      const buyTokenAddress = item.split("#")[1];

      const sellTokenSymbol = await contractQuery(
        sellTokenAddress,
        "ft_metadata"
      );
      const buyTokenSymbol = await contractQuery(
        buyTokenAddress,
        "ft_metadata"
      );

      const allOrders = contract.get_orders({
        sell_token: sellTokenAddress,
        buy_token: buyTokenAddress,
      });

      const finishedOrders = contract.get_filled_orders({
        sell_token: sellTokenAddress,
        buy_token: buyTokenAddress,
      });

      Promise.all([allOrders, finishedOrders]).then((value) => {
        const allOrders = value[0];
        const finishedOrders = value[1];
        let ordersByUser = [];

        if (allOrders === null && finishedOrders === null) {
          return setOrders(null);
        }

        if (allOrders !== null) {
          ordersByUser = allOrders.filter(
            (order) => order.order.maker === currentUser.accountId
          );
        }

        let finishedOrdersByUser = [];
        if (finishedOrders !== null) {
          finishedOrdersByUser = finishedOrders.filter(
            (finishedOrder) =>
              finishedOrder.order.maker === currentUser.accountId ||
              finishedOrder.order.matcher === currentUser.accountId
          );
        }

        if (ordersByUser.length !== 0) {
          for (const order of ordersByUser) {
            (async (item) => {
              const parsedBuyAmount = await toPrecision(
                item.order.buy_amount,
                item.order.buy_token
              );
              const parsedSellAmount = await toPrecision(
                item.order.sell_amount,
                item.order.sell_token
              );

              result.push({
                id: item.order_id,
                type: item.order.action,
                buy_token: buyTokenSymbol.symbol,
                buy_token_address: buyTokenAddress,
                buy_amount: parsedBuyAmount,
                sell_token: sellTokenSymbol.symbol,
                sell_token_address: sellTokenAddress,
                sell_amount: parsedSellAmount,
                status: item.order.status,
                creationTime: item.order.creation_time,
              });
              setOrders(result);
            })(order);
          }
        }

        if (finishedOrdersByUser.length === 0 && ordersByUser.length === 0) {
          setOrders([]);
        }

        if (finishedOrdersByUser.length !== 0) {
          for (const finishedOrder of finishedOrdersByUser) {
            (async (item) => {
              const parsedBuyAmount = await toPrecision(
                item.order.buy_amount,
                item.order.buy_token
              );
              const parsedSellAmount = await toPrecision(
                item.order.sell_amount,
                item.order.sell_token
              );
              if (item.order.maker === currentUser.accountId) {
                result.push({
                  id: item.order_id,
                  type: item.order.action,
                  buy_token: sellTokenSymbol.symbol,
                  buy_token_address: buyTokenAddress,
                  buy_amount: parsedBuyAmount,
                  sell_token: buyTokenSymbol.symbol,
                  sell_token_address: sellTokenAddress,
                  sell_amount: parsedSellAmount,
                  status: item.order.status,
                  creationTime: item.order.creation_time,
                });
                setOrders(result);
              } else if (item.order.matcher === currentUser.accountId) {
                result.push({
                  id: item.order_id,
                  type: item.order.action === "buy" ? "sell" : "buy",
                  buy_token: buyTokenSymbol.symbol,
                  buy_token_address: sellTokenAddress,
                  buy_amount: parsedSellAmount,
                  sell_token: sellTokenSymbol.symbol,
                  sell_token_address: buyTokenAddress,
                  sell_amount: parsedBuyAmount,
                  status: item.order.status,
                  creationTime: item.order.creation_time,
                });
                setOrders(result);
              }
            })(finishedOrder);
          }
        }
      });
    });
  };

  const sliceOrders = (currentAction) => {
    const numberOfOrdersPerOneAction = 5;

    const startIndex = (currentAction - 1) * numberOfOrdersPerOneAction;
    const endIndex = startIndex + numberOfOrdersPerOneAction;

    const finishedOrders = orders
      .filter((order) => order.status === "Finished")
      .sort((a, b) => b.creationTime - a.creationTime);
    const newOrders = orders
      .filter((order) => order.status === "New")
      .sort((a, b) => b.creationTime - a.creationTime);

    for (let i = 0; i < finishedOrders.length; i++) {
      newOrders.push(finishedOrders[i]);
    }

    return newOrders.slice(startIndex, endIndex);
  };

  const getMoreOrders = () => {
    const numberOfOrders = orders.length;
    const numberOfPaginationActions = Math.ceil(numberOfOrders / 5);
    const currentAction = parseInt(localStorage.getItem("seemoreAction"));

    if (currentAction > numberOfPaginationActions) {
      const currentAction = 1;
      const slicedOrders = sliceOrders(currentAction);
      localStorage.setItem("seemoreAction", (currentAction + 1).toString());

      setOrderComponent(
        <Orders orders={slicedOrders} contract={contract} isPart={true} />
      );
      return;
    }

    const slicedOrders = sliceOrders(currentAction);

    localStorage.setItem("seemoreAction", (currentAction + 1).toString());
    setOrderComponent(
      <Orders orders={slicedOrders} contract={contract} isPart={true} />
    );
  };

  const getFtBalances = async () => {
    const tokens = await getFtTokens(currentUser.accountId);
    let result = [];

    for (let i = 0; i < tokens.length; i++) {
      try {
        let metadata = contractQuery(tokens[i], "ft_metadata");
        let response = contractQuery(tokens[i], "ft_balance_of", {
          account_id: currentUser.accountId,
        });

        const res = await Promise.all([metadata, response]);
        metadata = res[0];
        response = res[1];
        const validAmount = await toPrecision(response, tokens[i]);
        if (parseInt(response) !== 0) {
          if (tokens[i] === "wusdt.testnet") {
            setWUSDTTokenData({
              symbol: metadata.symbol,
              icon: metadata.icon,
              address: tokens[i],
            });
          }

          result.push({
            id: i + 1,
            token: metadata.symbol,
            amount: validAmount,
            icon: metadata.icon,
            address: tokens[i],
          });
        }
      } catch (e) {
        console.warn(e);
        continue;
      }
    }
    setBalances(result);
    localStorage.setItem("seemoreAction", 2);
  };

  const getFtTokens = async (accountId) => {
    const tokens = await axios.post(
      "http://localhost:2400/",
      { name: accountId },
      {
        headers: {
          "Access-Control-Allow-Origin": "*",
        },
      }
    );

    return tokens.data;
  };

  const toPrecision = async (value, tokenAddress, fixed = 6) => {
    const result = await contractQuery(tokenAddress, "ft_metadata");
    return Big(value).div(Big(10).pow(result.decimals)).round(fixed).toFixed();
  };

  const fromPrecision = async (value, tokenAddress, fixed = 6) => {
    const result = await contractQuery(tokenAddress, "ft_metadata");
    return Big(value).mul(Big(10).pow(result.decimals)).round(fixed).toFixed();
  };

  const isNeedToDeposit = async (contract, account) => {
    const balance = await contractQuery(contract, "storage_balance_of", {
      account_id: account,
    });
    return !balance;
  };

  const createTransaction = async (receiverId, actions, nonceOffset = 1) => {
    const account = wallet.account();
    const connection = wallet.account().connection;

    const localKey = await connection.signer.getPublicKey(
      account.accountId,
      connection.networkId
    );

    const accessKey = await account.accessKeyForTransaction(
      receiverId,
      actions,
      localKey
    );

    if (!accessKey) {
      throw new Error(
        `Cannot find matching key for transaction sent to ${receiverId}`
      );
    }

    const block = await connection.provider.block({ finality: "final" });
    const blockHash = nearAPI.utils.serialize.base_decode(block.header.hash);

    const publicKey = PublicKey.from(accessKey.public_key);
    const nonce = accessKey.access_key.nonce + nonceOffset;

    return nearAPI.transactions.createTransaction(
      account.accountId,
      publicKey,
      receiverId,
      nonce,
      actions,
      blockHash
    );
  };

  const actionsToTransaction = async (receiverId, actions) => {
    return await createTransaction(
      receiverId,
      actions.map((action) =>
        transactions.functionCall(
          action.methodName,
          action.args,
          action.gas,
          action.deposit
        )
      )
    );
  };

  const sendTransaction = async () => {
    const errorBlock = document.getElementById("error-block");
    const parsedAmount = parseFloat(userAmount);
    const parsedPrice = parseFloat(userPrice);

    const defaultPrice = localStorage.getItem("inputDefaultPrice");
    const defaultAmount = localStorage.getItem("inputDefaultAmount");

    if (
      (isNaN(userAmount) && isNaN(userPrice)) ||
      (userAmount === "" &&
        userPrice === "" &&
        defaultAmount === null &&
        defaultPrice === null)
    ) {
      errorBlock.classList.remove("not-visible");
      return setErrorText("incorrect amount and price");
    }

    if (
      userAmount === "" &&
      defaultAmount !== null &&
      isNaN(userPrice) &&
      defaultPrice === null
    ) {
      errorBlock.classList.remove("not-visible");

      return setErrorText(`incorrect  price`);
    }

    if (
      isNaN(userAmount) &&
      defaultPrice !== null &&
      userPrice === "" &&
      defaultAmount === null
    ) {
      errorBlock.classList.remove("not-visible");

      return setErrorText(`incorrect amount`);
    }

    if (userPrice === "" && defaultPrice === null) {
      errorBlock.classList.remove("not-visible");

      return setErrorText(`incorrect price`);
    }
    if (userAmount === "" && defaultAmount === null) {
      errorBlock.classList.remove("not-visible");

      return setErrorText(`incorrect amount`);
    }

    if (isNaN(userAmount) || isNaN(userPrice)) {
      errorBlock.classList.remove("not-visible");

      return setErrorText(
        `incorrect ${isNaN(userAmount) ? "amount" : "price"}`
      );
    }

    if (parsedAmount === 0 && parsedPrice === 0) {
      errorBlock.classList.remove("not-visible");
      return setErrorText(`incorrect amount and price`);
    }

    if (parsedAmount === 0 || parsedPrice === 0) {
      errorBlock.classList.remove("not-visible");
      return setErrorText(
        `incorrect ${userAmount === 0 ? "amount" : "price"} `
      );
    }

    if (userAction === "buy") {
      const sellTokenBalace = balances.filter(
        (item) => item.address === wUSDTTokenData.address
      );

      if (
        sellTokenBalace.length === 0 ||
        parseFloat(sellTokenBalace[0].amount) < parsedPrice ||
        parseFloat(sellTokenBalace[0].amount) < defaultPrice
      ) {
        errorBlock.classList.remove("not-visible");
        return setErrorText(`not enough balance [${wUSDTTokenData.symbol}]`);
      }
    } else if (userAction === "sell") {
      const buyTokenBalace = balances.filter(
        (item) => item.address === currentNotStableToken.address
      );

      if (
        buyTokenBalace.length === 0 ||
        parseFloat(buyTokenBalace[0].amount) < parsedAmount ||
        parseFloat(buyTokenBalace[0].amount) < defaultAmount
      ) {
        errorBlock.classList.remove("not-visible");
        return setErrorText(
          `not enough balance [${currentNotStableToken.symbol}]`
        );
      }
    }

    if (!errorBlock.classList.contains("not-visible")) {
      errorBlock.classList.add("not-visible");
    }

    let msg = {
      sell_token:
        userAction === "buy"
          ? wUSDTTokenData.address //! swap
          : currentNotStableToken.address,
      sell_amount:
        userAction === "buy"
          ? await fromPrecision(
              defaultPrice !== null ? parseFloat(defaultPrice) : userPrice,
              wUSDTTokenData.address
            )
          : await fromPrecision(
              defaultAmount !== null ? parseFloat(defaultAmount) : userAmount,
              currentNotStableToken.address
            ),
      raw_sell_amount:
        userAction === "buy"
          ? defaultPrice !== null
            ? defaultPrice
            : userPrice
          : defaultAmount !== null
          ? defaultAmount
          : userAmount,
      buy_token:
        userAction === "buy"
          ? currentNotStableToken.address
          : wUSDTTokenData.address,
      buy_amount:
        userAction === "buy"
          ? await fromPrecision(
              defaultAmount !== null ? parseFloat(defaultAmount) : userAmount,
              currentNotStableToken.address
            )
          : await fromPrecision(
              defaultPrice !== null ? parseFloat(defaultPrice) : userPrice,
              wUSDTTokenData.address
            ),
      raw_buy_amount:
        userAction === "buy"
          ? defaultAmount !== null
            ? defaultAmount
            : userAmount
          : defaultPrice !== null
          ? defaultPrice
          : userPrice,
      action: userAction,
      creation_time: Date.now().toString(),
      status: "New",
    };

    let orders = await contract.get_orders({
      sell_token: msg.sell_token,
      buy_token: msg.buy_token,
    });

    const orderBookOrders = await contract.get_orders_to_orderbook({
      sell_token: msg.buy_token,
      buy_token: msg.sell_token,
    });

    let orderBookId;
    if (orderBookOrders !== null) {
      if (
        userAction === "buy" &&
        defaultPrice !== null &&
        defaultAmount !== null
      ) {
        orderBookId = orderBookOrders.filter(
          (order) =>
            parseFloat(order.order.buy_amount) === parseFloat(defaultPrice) &&
            parseFloat(order.order.sell_amount) === parseFloat(defaultAmount)
        );
      } else if (
        userAction === "buy" &&
        defaultAmount === null &&
        defaultPrice === null
      ) {
        orderBookId = orderBookOrders.filter(
          (order) =>
            parseFloat(order.order.buy_amount) === parsedPrice &&
            parseFloat(order.order.sell_amount) === parsedAmount
        );
      } else if (
        userAction === "sell" &&
        defaultAmount !== null &&
        defaultPrice !== null
      ) {
        orderBookId = orderBookOrders.filter(
          (order) =>
            parseFloat(order.order.buy_amount) === parseFloat(defaultAmount) &&
            parseFloat(order.order.sell_amount) === parseFloat(defaultPrice)
        );
      } else if (
        userAction === "sell" &&
        defaultAmount === null &&
        defaultPrice === null
      ) {
        orderBookId = orderBookOrders.filter(
          (order) =>
            parseFloat(order.order.buy_amount) === parsedAmount &&
            parseFloat(order.order.sell_amount) === parsedPrice
        );
      }
    }

    let reversedOrders = await contract.get_orders({
      sell_token: msg.buy_token,
      buy_token: msg.sell_token,
    });

    let mustExit = false;
    let maker;
    if (reversedOrders !== null) {
      reversedOrders.map((reversedOrder) => {
        const revesredOrderCondition =
          reversedOrder.order.buy_amount === msg.sell_amount &&
          reversedOrder.order.sell_amount === msg.buy_amount &&
          reversedOrder.order.maker !== currentUser.accountId;
        if (revesredOrderCondition) {
          msg = {
            order_id: reversedOrder.order_id,
            orderbook_id: orderBookId[0].order_id,
            matching_time: Date.now().toString(),
          };
          maker = reversedOrder.order.maker;
        }
      });
    }
    if (orders !== null) {
      orders.map((order) => {
        if (
          order.order.buy_amount === msg.buy_amount &&
          order.order.sell_amount === msg.sell_amount &&
          order.order.maker === currentUser.accountId
        ) {
          errorBlock.classList.remove("not-visible");
          setErrorText("order already exists");
          mustExit = true;
        }
      });
    }

    if (mustExit) {
      return;
    }

    localStorage.setItem("msg", JSON.stringify(msg));

    initTokenContract().then(async ({ tokenContract, stableTokenConfig }) => {
      const parsedAmount = await fromPrecision(
        defaultAmount !== null ? defaultAmount : userAmount,
        stableTokenConfig.contractName
      );
      const parsedPrice = await fromPrecision(
        defaultPrice !== null ? defaultPrice : userPrice,
        stableTokenConfig.contractName
      );

      const args = msg.hasOwnProperty("sell_token")
        ? userAction === "buy"
          ? {
              receiver_id: config.contractName,
              amount: msg.sell_amount,
              msg: JSON.stringify(msg),
            }
          : {
              receiver_id: config.contractName,
              amount: msg.sell_amount,
              msg: JSON.stringify(msg),
            }
        : userAction === "buy"
        ? {
            receiver_id: config.contractName,
            amount:
              defaultPrice !== null
                ? await fromPrecision(
                    defaultPrice,
                    stableTokenConfig.contractName
                  )
                : parsedPrice,
            msg: JSON.stringify(msg),
          }
        : {
            receiver_id: config.contractName,
            amount:
              defaultAmount !== null
                ? await fromPrecision(
                    defaultAmount,
                    stableTokenConfig.contractName
                  )
                : parsedAmount,
            msg: JSON.stringify(msg),
          };

      const depositToContractArgs = JSON.stringify({
        account_id: config.contractName,
        registration_only: true,
      });

      const depositToMakerArgs = JSON.stringify({
        account_id: maker,
        registration_only: true,
      });

      const depositToMatcherArgs = {
        account_id: currentUser.accountId,
        registration_only: true,
      };

      const needToDepositToContract = await isNeedToDeposit(
        stableTokenConfig.contractName,
        config.contractName
      );

      const needToDepositToMathcer = await isNeedToDeposit(
        userAction === "buy"
          ? currentNotStableToken.address
          : stableTokenConfig.contractName,
        currentUser.accountId
      );

      let needToDepositToMaker;
      if (maker) {
        needToDepositToMaker = await isNeedToDeposit(
          stableTokenConfig.contractName,
          maker
        );
      }

      const depositAmount = await contractQuery(
        userAction === "buy"
          ? currentNotStableToken.address
          : stableTokenConfig.contractName,
        "storage_balance_bounds",
        {}
      );

      if (needToDepositToContract || needToDepositToMaker) {
        try {
          await contract.account.signAndSendTransaction({
            receiverId: stableTokenConfig.contractName,
            actions: [
              transactions.functionCall(
                "storage_deposit",
                needToDepositToContract
                  ? Buffer.from(depositToContractArgs)
                  : Buffer.from(depositToMakerArgs),
                DEPOSIT_GAS,
                depositAmount.min
              ),

              transactions.functionCall(
                "ft_transfer_call",
                Buffer.from(JSON.stringify(args)),
                TGAS,
                ONE_YOCTO
              ),
            ],
          });

          return;
        } catch (e) {
          console.log(e);
          errorBlock.classList.remove("not-visible");
          return setErrorText(`Transaction failed`);
        }
      }

      if (needToDepositToMathcer) {
        const depositAction = {
          args: depositToMatcherArgs,
          gas: DEPOSIT_GAS,
          methodName: "storage_deposit",
          deposit: depositAmount.min,
        };

        const depositTransaction = await actionsToTransaction(
          currentNotStableToken.address,
          [depositAction]
        );

        const transferAction = {
          args,
          gas: TGAS,
          deposit: ONE_YOCTO,
          methodName: "ft_transfer_call",
        };

        const transferTransaction = await actionsToTransaction(
          stableTokenConfig.contractName,
          [transferAction]
        );

        return await wallet.requestSignTransactions([
          depositTransaction,
          transferTransaction,
        ]);
      }

      await tokenContract.ft_transfer_call(
        Buffer.from(JSON.stringify(args)),
        TGAS,
        ONE_YOCTO
      );
    });
  };
  const sortOrders = () => {
    let swapped = false;
    do {
      swapped = false;
      for (let i = 0; i < ORDER_BOOK.length - 1; i++) {
        if (ORDER_BOOK[i].price > ORDER_BOOK[i + 1].price) {
          let temp = ORDER_BOOK[i].price;
          ORDER_BOOK[i].price = ORDER_BOOK[i + 1].price;
          ORDER_BOOK[i + 1].price = temp;
          swapped = true;
        }
      }
    } while (swapped);
    setOrderBook(ORDER_BOOK.reverse());
  };

  return (
    <div className="container">
      <div id="wrapper">
        <div className="header">
          <div className="logo app-logo">
            <img src={logo} alt="Logo" className="icon" />
            <div className="logo-text">
              <a href="http://jove-06.github.io/web-crypto-market">
                <h1 className="bold">Near</h1>
                <h2 className="thin">Market</h2>
              </a>
            </div>
          </div>

          <div className="btn-block">
            {currentUser ? (
              <div className="user-info">
                <div className="user-icon logged-in-user-icon">
                  <span className="circle" />
                  <span className="elipse" />
                </div>
                <div className="dd-wrapper">
                  <button
                    className="current-user-name"
                    onClick={onDropDownClick}
                  >
                    {currentUser.accountId.length > 16 ? (
                      <>{SHORT_NAME}... </>
                    ) : (
                      <>{currentUser.accountId}</>
                    )}
                    <HiChevronDown className="dd-closed-icon" id="dd-icon" />
                  </button>
                  <div className="not-visible" id="dd-list">
                    <button className="dd-list-item" onClick={signOut}>
                      Sign out <BsArrow90DegRight icon="fas fa-arrow-right" />
                    </button>
                  </div>
                </div>
              </div>
            ) : (
              <button
                className="default-button connect-wallet-button"
                onClick={signIn}
              >
                Connect wallet
              </button>
            )}
          </div>
        </div>
        <div className="row">
          <div className="switcher">
            <button
              className="buy"
              id="buy-btn"
              onClick={() => {
                const buyButton = document.getElementById("buy-btn");
                const sellButton = document.getElementById("sell-btn");
                const buyBg = document.getElementById("buy-bg");
                const sellBg = document.getElementById("sell-bg");
                const sellText = document.getElementById("input-buy-text");

                sellButton.classList = ["grey-input"];
                sellBg.classList = ["sell-icon small-icon"];
                sellText.innerHTML = "Buy";

                buyButton.classList = ["buy"];
                buyBg.classList.add("blue-bg");
                setUserAction("buy");
              }}
            >
              <div className="buy-icon small-icon blue-bg" id="buy-bg">
                <img
                  src={BuyCheckmarkIcon}
                  alt="buy-checkmark"
                  className="buy-checkmark"
                />
              </div>
              Buy
            </button>
            <button
              id="sell-btn"
              onClick={() => {
                const sellButton = document.getElementById("sell-btn");
                const buyButton = document.getElementById("buy-btn");
                const sellBg = document.getElementById("sell-bg");
                const buyBg = document.getElementById("buy-bg");
                const buyText = document.getElementById("input-buy-text");

                buyButton.classList = ["grey-input"];
                buyBg.classList = ["buy-icon small-icon"];
                buyText.innerText = "Sell";

                sellButton.classList = ["buy"];
                sellBg.classList.add("red-bg");

                setUserAction("sell");
              }}
              className="grey-input"
            >
              <div className="sell-icon small-icon" id="sell-bg">
                <img
                  src={SellCheckmarkIcon}
                  alt="sell-checkmark"
                  className="sell-checkmark"
                />
              </div>
              Sell
            </button>
          </div>
          <div className="all">
            <div className="open-order">
              <div>
                {currentUser ? (
                  <>
                    <div className="input">
                      <div className="preview-text">
                        <h5 className="input-preview" id="input-buy-text">
                          Buy
                        </h5>
                      </div>
                      <input
                        placeholder="Amount"
                        id="amount-input"
                        onChange={(text) => {
                          const totalText =
                            document.getElementById("total-text");
                          const priceInput =
                            document.getElementById("price-input");

                          localStorage.removeItem("inputDefaultAmount");
                          if (priceInput.value !== "") {
                            totalText.classList = ["total-text"];
                            setTotal(text.target.value * priceInput.value);
                          } else if (
                            text.target.value === "" &&
                            priceInput.value === ""
                          ) {
                            totalText.innerHTML = "";
                          }

                          setUserAmount(text.target.value);
                        }}
                      />
                      <div className="info">
                        <p className="max-amount" id="max-amount">
                          243{" "}
                          {currentNotStableToken === null ? (
                            <></>
                          ) : (
                            currentNotStableToken.symbol
                          )}
                        </p>
                        <p className="max-amount max">MAX</p>

                        {currentNotStableToken !== null ? (
                          <img
                            src={currentNotStableToken.icon}
                            className="near-coin-icon"
                            id="near-coin-icon"
                          />
                        ) : (
                          <></>
                        )}
                        <p className="max-amount coin-text" id="coin-text">
                          {currentNotStableToken === null ? (
                            <></>
                          ) : (
                            currentNotStableToken.symbol
                          )}
                        </p>
                      </div>
                    </div>

                    <div className="input">
                      <div className="preview-text">
                        <h5 className="input-preview">For</h5>
                      </div>
                      <input
                        placeholder="Price"
                        id="price-input"
                        onChange={(text) => {
                          const totalText =
                            document.getElementById("total-text");
                          localStorage.removeItem("inputDefaultPrice");
                          const amountInput =
                            document.getElementById("amount-input");

                          if (amountInput.value !== "") {
                            totalText.classList = ["total-text"];
                            setTotal(text.target.value * amountInput.value);
                          } else if (
                            amountInput.value === "" &&
                            text.target.value === ""
                          ) {
                            totalText.innerHTML = "";
                          }

                          setUserPrice(text.target.value);
                        }}
                      />
                      <div className="info">
                        <p className="max-amount">243 wUSDT</p>
                        <p className="max-amount max">MAX</p>
                        {wUSDTTokenData.icon ? (
                          <img
                            src={wUSDTTokenData.icon}
                            className="usdt-coin-icon"
                            alt="Usdt icon"
                          />
                        ) : (
                          <></>
                        )}
                        <p className="max-amount coin-text">wUSDT</p>
                      </div>
                    </div>
                    <div className="total-block">
                      <div>
                        <h5 className="total-preview">Total:</h5>
                      </div>
                      <h4 className="total-text" id="total-text">
                        {total}
                      </h4>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="input">
                      <div className="preview-text">
                        <h5 className="input-preview" id="input-buy-text">
                          Buy
                        </h5>
                      </div>
                      <input defaultValue={100} id="amount-input" />
                      <div className="info">
                        <p className="max-amount" id="max-amount">
                          243 NEAR
                        </p>
                        <p className="max-amount max">MAX</p>

                        <img
                          src={nearIcon}
                          className="near-coin-icon"
                          id="near-coin-icon"
                        />

                        <p className="max-amount coin-text" id="coin-text">
                          NEAR
                        </p>
                      </div>
                    </div>

                    <div className="input">
                      <div className="preview-text">
                        <h5 className="input-preview">For</h5>
                      </div>
                      <input defaultValue={100} id="price-input" />
                      <div className="info">
                        <p className="max-amount">243 USDT</p>
                        <p className="max-amount max">MAX</p>

                        <img
                          src={usdtIcon}
                          className="usdt-coin-icon"
                          alt="Usdt icon"
                        />

                        <p className="max-amount coin-text">USDT</p>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </div>

            <div className="order-book">
              <div className="order-book-heading" id="head">
                <h5 className="component-heading">Order Book</h5>
                <div className="order-type-switcher">
                  <button
                    className="order-type-switcher-btn order-type-buy"
                    id="buy-order-type"
                    onClick={() => {
                      const sellOrdersBtn =
                        document.getElementById("sell-order-type");
                      const buyOrdersBtn =
                        document.getElementById("buy-order-type");

                      buyOrdersBtn.classList.add("order-type-buy");
                      sellOrdersBtn.classList = ["order-type-switcher-btn"];

                      setOrderBookAction("buy");
                    }}
                  >
                    Buy
                  </button>
                  <button
                    className="order-type-switcher-btn"
                    id="sell-order-type"
                    onClick={() => {
                      const sellOrdersBtn =
                        document.getElementById("sell-order-type");
                      const buyOrdersBtn =
                        document.getElementById("buy-order-type");

                      sellOrdersBtn.classList.add("order-type-sell");
                      buyOrdersBtn.classList = ["order-type-switcher-btn"];

                      setOrderBookAction("sell");
                    }}
                  >
                    Sell
                  </button>
                </div>
                <div className="tokens-dd-wrapper">
                  <button
                    id="tokens-dd"
                    className="tokens-dd"
                    onClick={() => {
                      const tokensList =
                        document.getElementById("dd-tokens-list");

                      if (
                        localStorage.getItem("isNeedToBeDisabled") === "true"
                      ) {
                        tokensList.setAttribute("disabled", "disabled");
                        return;
                      }
                      localStorage.removeItem("isNeedToBeDisabled");
                      if (
                        tokensList.classList[0] === "not-visible" &&
                        tokens.length > 6
                      ) {
                        tokensList.classList = [];
                        tokensList.classList.add("dd-tokens-list");
                        tokensList.classList.add("scrollable-block");
                        return;
                      } else if (tokensList.classList[0] === "not-visible") {
                        tokensList.classList = [];
                        tokensList.classList.add("dd-tokens-list");
                        return;
                      }
                      tokensList.classList = [];
                      tokensList.classList.add("not-visible");
                    }}
                  >
                    {currentNotStableToken === null ? (
                      <></>
                    ) : (
                      currentNotStableToken.symbol
                    )}
                    /wUSDT
                  </button>

                  <div className="not-visible" id="dd-tokens-list">
                    {tokens.map((token, index) => (
                      <li key={index} className="tokens-row">
                        {token.symbol !==
                        JSON.parse(localStorage.getItem("wusdtData")).symbol ? (
                          <>
                            <img src={token.icon} className=" tokens-icon" />
                            <div className="button-item-block">
                              <button
                                className="dd-tokens-button-item"
                                onClick={() => {
                                  const coinText =
                                    document.getElementById("coin-text");
                                  const maxAmount =
                                    document.getElementById("max-amount");
                                  const tokensButton =
                                    document.getElementById("tokens-dd");
                                  document.getElementById(
                                    "near-coin-icon"
                                  ).src = token.icon;
                                  const orderBookAmount =
                                    document.getElementById(
                                      "order-book-amount"
                                    );
                                  maxAmount.innerText = `243 ${token.symbol}`;
                                  coinText.innerText = token.symbol;
                                  tokensButton.innerText = `${token.symbol}/wUSDT`;
                                  orderBookAmount.innerText = `Amount (${token.symbol})`;
                                  setCurrentNotStableToken({
                                    symbol: token.symbol,
                                    icon: token.icon,
                                    address: token.address,
                                  });
                                  setTokensListAction(token.symbol);
                                  localStorage.setItem(
                                    "notStableToken",
                                    JSON.stringify({
                                      symbol: token.symbol,

                                      icon: token.icon,
                                      address: token.address,
                                    })
                                  );
                                }}
                              >
                                {token.symbol}/
                                {
                                  JSON.parse(localStorage.getItem("wusdtData"))
                                    .symbol
                                }
                              </button>
                            </div>
                          </>
                        ) : (
                          <></>
                        )}
                      </li>
                    ))}
                  </div>
                </div>
              </div>
              <div className="order-book-columns-names">
                <h5 className="order-book-text-columns">
                  {localStorage.getItem("wusdtData") ? (
                    JSON.parse(localStorage.getItem("wusdtData")).symbol
                  ) : (
                    <></>
                  )}{" "}
                  price
                </h5>

                <h5 className="order-book-text-columns" id="order-book-amount">
                  Amount (
                  {currentNotStableToken === null ? (
                    <></>
                  ) : (
                    currentNotStableToken.symbol
                  )}
                  )
                </h5>

                <h5 className="order-book-text-columns">Total</h5>
              </div>
              <div className="order-book-block">
                <OrderBook
                  orderBook={orderBook}
                  type={orderBookAction}
                  buyButton={document.getElementById("buy-btn")}
                  sellButton={document.getElementById("sell-btn")}
                  buyInput={document.getElementById("amount-input")}
                  sellInput={document.getElementById("price-input")}
                  totalText={document.getElementById("total-text")}
                />
              </div>
            </div>
          </div>
          {currentUser ? (
            <div className="order-btn-block">
              <button className="order-btn" onClick={sendTransaction}>
                Create order
              </button>
              <div className="error-message not-visible" id="error-block">
                <h4 className="error-message-default-text">Error:</h4>
                <h4 className="error-text">{errorText}</h4>
              </div>
            </div>
          ) : (
            <div className="order-btn-block">
              <button className="order-btn" onClick={signIn}>
                Create order
              </button>
            </div>
          )}
        </div>
        {currentUser ? (
          <div className="bottom-row">
            <div className="balances">
              <h2>My balances</h2>

              <Balances balances={balances} />
            </div>
            <div className="orders">
              <h5 className="component-heading">My orders</h5>
              <div id="orders-list">
                {orderComponent ? (
                  orderComponent
                ) : (
                  <Orders orders={orders} contract={contract} />
                )}
              </div>
              {orders !== null && orders.length > 5 ? (
                <div className="seemore-block">
                  <button className="seemore-button" onClick={getMoreOrders}>
                    See more ({localStorage.getItem("seemoreAction") - 1}) of (
                    {Math.ceil(orders.length / 5)})
                  </button>
                </div>
              ) : (
                <></>
              )}
            </div>
          </div>
        ) : (
          <div className="center-button">
            <button className="wallet-button" onClick={signIn}>
              Connect wallet
            </button>
            <div className="bottom-row blured-bg">
              <div className="balances">
                <h2>My balances</h2>
                <Balances balances={BALANCES} />
              </div>
              <div className="orders">
                <h5 className="component-heading">My orders</h5>
                <Orders orders={ORDER_LIST} />
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
