import React, { useEffect, useRef, useState } from "react";

import "../styles/app.css";

export const OrderBook = (props) => {
  if (props.orderBook !== null && typeof props.orderBook !== "undefined") {
    let result = [];

    if (!props.orderBook[0].hasOwnProperty("price")) {
      const orders = props.orderBook;

      for (const order of orders) {
        result.push({
          id: order.order_id,
          price: order.order.sell_amount,
          amount: order.order.buy_amount,
        });
      }
      if (props.type === "buy") {
        result.sort((a, b) => b.price - a.price);
      } else if (props.type === "sell") {
        result.sort((a, b) => b.amount - a.amount);
      }
    } else {
      result = props.orderBook;
    }

    // let swapped = false;
    // do {
    //   swapped = false;
    //   for (let i = orders.length - 1; i > 0; i--) {
    //     if (orders[i].price > orders[i - 1].price) {
    //       let temp = orders[i].price;
    //       orders[i].price = orders[i - 1].price;
    //       orders[i - 1].price = temp;
    //       swapped = true;
    //     }
    //   }
    // } while (swapped);

    const orderBook = result.map((item) => {
      let highestPrice;
      let width;
      if (props.type === "buy") {
        highestPrice = result[0].price;
        width = (item.price * 100) / highestPrice;
      } else if (props.type === "sell") {
        highestPrice = result[0].amount;
        width = (item.amount * 100) / highestPrice;
      }

      return (
        <div className="order-book-row" key={item.id}>
          {props.type === "buy" ? (
            <button
              className="order-book-button"
              onClick={() => {
                localStorage.setItem(
                  "inputDefaultAmount",
                  item.amount.toString()
                );
                localStorage.setItem(
                  "inputDefaultPrice",
                  item.price.toString()
                );

                if (
                  props.buyInput !== null &&
                  props.sellInput !== null &&
                  props.totalText !== null
                ) {
                  props.buyInput.value = item.amount;
                  props.sellInput.value = item.price;
                  props.totalText.innerHTML = item.price * item.amount;

                  props.sellButton.click();
                }
              }}
            >
              <li
                key={item.id}
                className="order-book-buy"
                id="order-book-buy"
                style={{
                  width: width.toString() + "%",
                  backgroundColor: "rgb(26, 175, 166, 0.15)",
                  left: (100 - width).toString() + "%",
                }}
              >
                <div className="order-book-item buy-type">{item.price}</div>
                <div className="order-book-item normal-type">{item.amount}</div>
                <div className="order-book-item normal-type">
                  {item.price * item.amount}
                </div>
              </li>
            </button>
          ) : (
            <button
              className="order-book-button"
              onClick={() => {
                localStorage.setItem(
                  "inputDefaultPrice",
                  item.amount.toString()
                );
                localStorage.setItem(
                  "inputDefaultAmount",
                  item.price.toString()
                );

                if (
                  props.buyInput !== null &&
                  props.sellInput !== null &&
                  props.totalText !== null
                ) {
                  props.buyInput.value = item.price;
                  props.sellInput.value = item.amount;
                  props.totalText.innerHTML = item.price * item.amount;

                  props.buyButton.click();
                }
              }}
            >
              <li
                key={item.id}
                className="order-book-buy"
                id="order-book-buy"
                style={{
                  width: width.toString() + "%",
                  backgroundColor: "rgb(227, 122, 122, 0.20)",
                  left: (100 - width).toString() + "%",
                }}
              >
                <div className="order-book-item sell-type">{item.amount}</div>
                <div className="order-book-item normal-type">{item.price}</div>
                <div className="order-book-item normal-type">
                  {item.price * item.amount}
                </div>
              </li>
            </button>
          )}
        </div>
      );
    });
    if (orderBook.length > 8) {
      return <ul className="order-book-ul scrollable-block">{orderBook}</ul>;
    }
    return <ul className="order-book-ul">{orderBook}</ul>;
  } else if (props.orderBook === null) {
    return (
      <div>
        <h3 className="no-orders-book">No orders here yet...</h3>
      </div>
    );
  } else if (typeof props.orderBook === "undefined") {
    return <></>;
  }
};
